const PICTOGRAMS: Record<string, { class: string; category: string }> = {};

for (const category in CATEGORIES) {
  if (Object.hasOwnProperty.call(CATEGORIES, category)) {
    const _pictograms = CATEGORIES[category];

    _pictograms.forEach(pictogram => {
      PICTOGRAMS[pictogram] = {
        class: pictogram,
        category: category,
      };
    });
  }
}

export { PICTOGRAMS };
